body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navBar ul{
    padding: 0;
    margin: 0;
    display: flex;
    list-style: none;
    width: 100%;
    background-color: #333;
    border: 5px solid #333333;    
  }

  .navBar ul a{
    text-decoration: none;
    color: #999;
    display: block;
    padding: 15px;
    margin: 0 5px;
    
  }

  .navBar ul .active, .navBar ul a:hover{
    background: #FFF;
    color: #222;
  }
  
  .contactUs{
    height:800px ;
    background-image:
    linear-gradient(
      #228B22, white, green
    );
    color: #fff;
  }

  .aboutUs{
    height:800px ;
    color: blue;
  }
  .aboutUs:hover {
    opacity: 1;
  }

  .card {
    background:#A0522D;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 40%;
    margin-left : auto;
    margin-right : auto;
    margin-top:10px;
  }

  .productCard {
    background:#222;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 40%;
    margin-left : auto;
    margin-right : auto;
    margin-top:10px;
  }

  .productCard > img{
    height:300px;
    width:350px;
  }
  
  .card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }
  
  .container {
    padding: 2px 16px;
  }
  
  .plates{
    margin:0;
    height: 100vh;
    width: 100%;
    background-attachment: fixed;
    background-image: url(/AAP/static/media/Products_Plates.4f4a582a.jpeg);
    background-size: contain;
  }

  .cups{
    margin:0;
    height: 100vh;
    width: 100%;
    background-attachment: fixed;
    background-image: url(/AAP/static/media/Products_Cups.ea22a546.jpg);
    background-size: contain;
  }

  .teaCups{
    margin:0;
    height: 100vh;
    width: 100%;
    background-attachment: fixed;
    background-image: url(/AAP/static/media/Products_Tea_Cups.39de4051.jpg);
    background-size: contain;
  }

  .productTitle{
   font-size: 60px;
   background-color:transparent;
   padding:8px 24px;
   border-radius: 16px;
  }

  .productSpecs{
    font-size: 40px;
    background-color:transparent;
    padding:8px 24px;
    border-radius: 16px;
  }
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

